:root {
    --boho-mexican-sand: #B29780;
    --boho-celadon: #A8A99E;
    --boho-tent-green: #A69F88;
    --boho-cocoa-cream: #DBC8B6;
    --boho-japanese-poet: #C4BCB7;
    --boho-tannery-brown: #5D4B3C;
} 

.home {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    position: relative;
}

.featured {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
    height: 600px;
    border-radius: 80% 10% 30% 10%;
    padding: 12px;
    /* animation: mymove 15s infinite; */
}

.text {
    position: absolute;
    top: 50%;
    left: 50%; 
    transform: translate(-50%, -50%);
    z-index: 1;
}

.text * {
    /* color: var(--boho-japanese-poet); */
    color: white;
    text-align: center;

}

#travelling {
    margin-left: -125%;
    font-size: 90px;
    font-weight: 900;
    font-style: italic;
    color: var(--boho-tannery-brown);
}

#goa {
    margin-right: -100%;
    font-size: 100px;
    font-style: italic;
    color: var(--boho-tannery-brown);
}

