.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
    background-color: var(--boho-japanese-poet);
}

.navbar * {
    color: black;
    text-decoration: none;
}

.logo {
    font-weight: 600;
    font-size: 24px;
    transition: 0.3s ease;
}

.logo:hover {
    cursor: pointer;
    letter-spacing: 2px;
    transition: 0.3s ease;
}